import {Component, OnInit} from '@angular/core';
import anime from 'animejs';
import * as AOS from 'aos';



import {environment} from "../environments/environment";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{


  Procent:any ={
    charged: '0%',

  }
  ngOnInit() {
    this.Map()
    this.StartAnimations()
    AOS.init()

  }

  StartAnimations(){

    var tl = anime.timeline({
      easing: 'easeOutExpo',
      duration: 750
    });
  tl.add({
    targets: this.Procent,
    charged: '100%',
    cycles: 130,
    round: 1,
    duration:2000,
    easing: 'linear',
  })
  tl.add({
    targets:'.Allplace',
    opacity:0,
    duration:1000,
    zIndex:-1

  })
  }
  title = 'MakeCityLookBetter';
  Map(){

    Object.assign(mapboxgl, {
      accessToken: environment.mapboxKey
    });

    const map = new mapboxgl.Map({
      accessToken:environment.mapboxKey,

      style: 'mapbox://styles/fortyxd/cl2xqv469000615t7cf48cyb4',
      center: [37.4128099, 55.7395971],
      zoom: 17,
      pitch: 45,
      antialias: true,

      container: 'map-mapbox', // container ID


    });
    map.scrollZoom.disable();
    function rotateCamera(timestamp:any) {

      map.rotateTo((timestamp / 100) % 360, { duration: 0 });

      requestAnimationFrame(rotateCamera);
    }

    map.on('load', () => {
      rotateCamera(0);
      const layers:any = map.getStyle().layers;


      map.addLayer({
        'id': '3d-buildings',
        'source': 'composite',
        'source-layer': 'building',
        'filter': ['==', 'extrude', 'true'],
        'type': 'fill-extrusion',
        'minzoom': 15,
        'paint': {
          'fill-extrusion-color': '#aaa',
          'fill-extrusion-height': [
            'interpolate',
            ['linear'],
            ['zoom'],
            15,
            0,
            15.05,
            ['get', 'height']
          ],
          'fill-extrusion-base': [
            'interpolate',
            ['linear'],
            ['zoom'],
            15,
            0,
            15.05,
            ['get', 'min_height']
          ],
          'fill-extrusion-opacity': 0.6
        }
      });
    });

  }
}
